<template>
    <div class="boot-admin">
        <iframe :src="bootAdminUrl" frameborder="0"></iframe>
    </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'boot',
    data () {
      return {
        bootAdminUrl: ''
      }
    },
    mounted () {
      axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      axios.get('/appConfig/getMonitorHosts', {params: {name: 'redefine.monitor.flow'}}).then(res => {
        this.bootAdminUrl = res.data
      })
    }
    // computed: {
    //   bootAdminUrl () {
    //     var map = {
    //       'dev-watchdog.welike.in': 'http://dev-flow.rdfamily.cn/hystrix/monitor?stream=http://dev-flow.rdfamily.cn/turbine.stream',
    //       'pre-watchdog.welike.in': 'http://pre-flow.rdfamily.cn/hystrix/monitor?stream=http://pre-flow.rdfamily.cn/turbine.stream',
    //       'watchdog.welike.in': 'http://flow.rdfamily.cn/hystrix/monitor?stream=http://flow.rdfamily.cn/turbine.stream'
    //     }
    //     if (window.env === 'development' || location.host.includes('localhost')) {
    //       return 'http://dev-flow.rdfamily.cn/hystrix/monitor?stream=http://dev-flow.rdfamily.cn/turbine.stream'
    //     }
    //     return map[location.host]
    //   }
    // }
  }
</script>

<style>
    .boot-admin {
        left: 0px;
        right: 0px;
        margin-top: -20px;
        width: 100%;
        height: 100%;
        position: absolute;
    }

    .boot-admin iframe {
        height: 100%;
        width: 100%;
        overflow: auto;
    }

    .el-main {
        position: relative;
    }
</style>


